.header {
    padding: 35px 0 40px;


    &-container {
        align-items: center;
    }
    &-logo__img{
        display: flex;
        align-items: center;
    }
    &__item {
        display: flex;
        align-items: center;
        gap: 15px;

        &-text {
            font-family: "ProximaNovaCond" , sans-serif;
            font-size: 15px;
        }
    }
    &__item-icon{
        display: flex;
        flex-shrink: 0;
        align-items: center;
        justify-content: center;
        width: 36px;
        height: 36px;
        border-radius: 50%;
        background-color: #fff;

        i {
            font-size: 14px;
            color: var(--c-primary);
        }
    }

    &__info{
        display: flex;
        gap: 100px;
        justify-content: flex-end;


        &-left{
            display: flex;
            row-gap: 10px;
            flex-direction: column;
        }
        &-right{
            display: flex;
            row-gap: 10px;
            flex-direction: column;
        }
        &__item {
            display: flex;
            align-items: center;
            gap: 15px;


            &-num{
                transition: 0.3s ease-in;
                cursor: pointer;
                font-size: 30px;
                font-weight: 700;
                color: #000;
                &:hover{
                    opacity: 0.7;
                    color: inherit;
                }

            
            }
            &-text{
                font-family: 'ProximaNovaCond',sans-serif;
                font-size: 15px;
                line-height: 1;

            }
        }

        &__phone,
        &__address{
            display: flex;
        }
    }
}
